@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
html {
	font-size: 62.5%;
}

* {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

body {
	font-family: 'Lato', 'Helvetica Neue', sans-serif;
	background-color: #f4f4f7;
	margin: 0;
}

.container {
	width: 80%;
	margin: 0 auto;
}

h1 {
	font-size: 2.8rem;
	font-family: 'Anton', sans-serif;
	letter-spacing: 0.8px;
	text-transform: uppercase;
	margin: 10px 0 0 0;
}

p,
span,
a {
	font-size: 1.8rem;
	margin: 5px 0;
}

.font-weight-light {
	font-weight: 400;
}

.font-weight-bold {
	font-weight: 700;
}

.font-color-primary {
	color: #ffffff;
}

a.link-unstyled {
	color: inherit;
	text-decoration: none;
}

a.link-unstyled:hover {
	color: inherit;
	text-decoration: none;
}

.btn {
	display: block;
	padding: 1.1rem 2rem;
	border-radius: 4px;
	margin: 10px auto 0 auto;
	width: 150px;
	text-align: center;
}

.btn.btn-primary {
	color: #fff;
	background-color: #7dcd40;
	border-color: #7dcd40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
	color: #fff;
	background-color: #7dcd40;
	border-color: #7dcd40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
	color: #fff;
	background-color: #7dcd40;
	border-color: #7dcd40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
	box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
	color: #fff;
	background-color: #cd4040;
	border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
	color: #fff;
	background-color: #cd4040;
	border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
	color: #fff;
	background-color: #cd4040;
	border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
	box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}

.Eventbar {
	min-height: 70px;
	background: #172e42;

	display: -webkit-flex;

	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.Eventbar__brand {
	font-size: 1.7rem;
	text-decoration: none;
	margin-left: 10px;

	display: -webkit-flex;

	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.Eventbar__brand:hover {
	color: #d6d6d6;
}

.Eventbar__brand > span {
	margin: 0 2.5px;
}

.HomePage-hero {
	width: 100%;
	height: calc(100vh - (70px + 150px));
	padding-top: 150px;
	background: url(/platzi-badges/static/media/stars.1d17360a.svg), #1b1b25;
	background-repeat: repeat;
}

.HomePage__container {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.HomePage__introduce {
	width: 35%;
	max-width: 450px;
	color: #ffff;
}

.HomePage-conf {
	width: 90%;
}

.HomePage-logo {
	width: 60%;
	max-width: 700px;
}

.HomePage-logo > img {
	width: 100%;
}

.Badge-hero {
	width: 100%;
	padding: 30px 0;
	background: url(/platzi-badges/static/media/stars.1d17360a.svg), #1b1b25;
	background-repeat: repeat;
}

.Badge-logo {
	display: block;
	width: 230px;
	margin: 0 auto;
}

.PageLoading {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	min-height: 60vh;
	width: 100%;
}

@-webkit-keyframes Loader {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes Loader {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.Loader {
	width: 50px;
	height: 50px;
	border-radius: 50%;
	border: 5px solid #d8e2d5;
	border-top: 5px solid #172e42;

	-webkit-animation: Loader 2s ease infinite;

	        animation: Loader 2s ease infinite;
}

